<template>
  <div class="casino container">
    <h1>欢迎来到押宝游戏</h1>
    <h4>填写押注金额后，然后点击1~9之间数字，通过Metamask支付就可参与。(测试网络：请将Metamask连接Rinkeby)</h4>
    押注: <input v-model="amount" placeholder="0 Ether">
    <ul>
      <li v-on:click="clickNumber">1</li>
      <li v-on:click="clickNumber">2</li>
      <li v-on:click="clickNumber">3</li>
      <li v-on:click="clickNumber">4</li>
      <li v-on:click="clickNumber">5</li>
      <li v-on:click="clickNumber">6</li>
      <li v-on:click="clickNumber">7</li>
      <li v-on:click="clickNumber">8</li>
      <li v-on:click="clickNumber">9</li>
    </ul>
    <p> 合约地址: 0x9835A61808750D841c89335F2BEaA9535a10210D <a href="https://rinkeby.etherscan.io/address/0x9835A61808750D841c89335F2BEaA9535a10210D" target="_blank"> ( 查看合约 )</a></p>
    <img v-if="pending" id="loader" src="https://loading.io/spinners/double-ring/lg.double-ring-spinner.gif">
    <div class="event" v-if="winEvent">
      <p> 本期中奖号码: {{winEvent._winningNumber}}</p>
      <p> 您的投注号码: {{winEvent._number}}</p>
      <p v-if="winEvent._status" id="has-won"><i aria-hidden="true" class="fa fa-check"></i> 恭喜，押中了，奖励 {{winEvent._amount_ether}} ether ( {{winEvent._amount}} wei)</p>
      <p v-else id="has-lost"><i aria-hidden="true" class="fa fa-times"></i> 很遗憾，没押中，再试一次.</p>
    </div>
    <p> -------------------------</p>
  </div>
</template>

<script>
export default {
  name: 'casino',
  data () {
    return {
      amount: null,
      pending: false,
      winEvent: null
    }
  },
  methods: {
    clickNumber (event) {
      console.log('BETTING ON NUMBER, AMOUNT', event.target.innerHTML, this.amount)
      this.winEvent = null
      this.pending = true
      this.$store.state.contractInstance().bet(event.target.innerHTML, {
        gas: 300000,
        value: this.$store.state.web3.web3Instance().toWei(this.amount, 'ether'),
        from: this.$store.state.web3.coinbase
      }, (err, result) => {
        if (err) {
          console.log(err)
          this.pending = false
        } else {
          let Won = this.$store.state.contractInstance().Won()
          Won.watch((err, result) => {
            if (err) {
              console.log('could not get event Won()')
            } else {
              this.winEvent = result.args
              this.winEvent._amount = parseInt(result.args._amount, 10)
              this.winEvent._amount_ether = result.args._amount / 1e18
              //this.winEvent._amount_ether = this.$store.state.web3.web3Instance().fromWei(result.args._amount, 'ether')
              this.winEvent._number = parseInt(result.args._number, 10)
              this.winEvent._winningNumber = parseInt(result.args._winningNumber, 10)
              //if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
              this.pending = false
              console.log('cj args', result.args)
              console.log('cj args', result.args)
            }
          })
        }
      })
    }
  },
  mounted () {
    console.log('dispatching getContractInstance')
    this.$store.dispatch('getContractInstance')
  }
}
</script>

<style scoped>
.casino {
     margin-top: 50px;
     text-align:center;
}
#loader {
  width:150px;
}
ul {
    margin: 50px;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap:100px;
    grid-row-gap:40px;
}
li{
    padding: 10px;
    margin-right: 50px;
    font-size: 30px;
    cursor: pointer;
    background-color:#fff;
    border: -2px solid #bf0d9b;
    color: #bf0d9b;
    box-shadow:5px 5px 3px 5px  #947d8f;
}
li:hover{
    background-color:#2f693d;
    color:white;
    box-shadow:0px 0px #bf0d9b;
}
li:active{
    opacity: 0.7;
}
*{
   color: #444444;
}
#has-won {
  color: green;
}
#has-lost {
  color:red;
}
</style>



